.events-slider {
  .events-slider__item,
  .events-slider__img {
    border-radius: 50%;
  }
  .events-slider__item {
    position: relative;
    transition: 0.2s ease-in-out;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 100%;
      border: 5px solid $blue-secondary;
      opacity: 0;
      visibility: hidden;
      transition: 0.2s;
      transition-delay: 0.2s;
    }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% + 15px);
      height: calc(100% + 15px);
      opacity: 0.3;
      border: 1px solid $blue;
      border-radius: 100%;
      transition: 0.2s;
      transition-delay: 0.2s;
    }
  }
  .events-slider__img {
    position: relative;
    padding-bottom: 100%;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 100%;
      background: linear-gradient(
        180deg,
        rgba(14, 14, 57, 0.37) 4.94%,
        rgba(15, 15, 60, 0.73) 51.47%,
        #11113f 100%
      );
      transition: 0.2s;
      transition-delay: 0.2s;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 100%;
      box-shadow: 0 0 0 5px #3c94bb, 0 0 90px 6px rgba(0, 154, 241, 0.48),
        0 0 21px -1px rgba(60, 148, 187, 0.7);
      transition: 0.2s;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  .events-slider__info {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    max-width: 90%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    z-index: 2;
    .btn {
      position: absolute;
      top: calc(100% - 120px);
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      max-width: 231px;
    }
  }
  .events-slider__action {
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transform: translateY(50px);
    transition: 0.15s 0.2s ease-in-out;
  }
  .events-slider__info-caption {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .events-slider__date {
    font-size: clamp(0.875rem, 2vw, 1.5rem);
    line-height: 140%;
    font-weight: 300;
    opacity: 0.85;
    display: flex;
    align-items: center;
    i {
      color: $blue;
      margin-right: 10px;
    }
  }
  .events-slider__artist {
    font-size: clamp(1.75rem, 3vw, 3.75rem);
    line-height: 140%;
    font-weight: 600;
  }
  .swiper-slide {
    .events-slider__item {
      transform: scale(1);
    }
  }
  .swiper-slide-active {
    .events-slider__item {
      z-index: 2;
      &::after {
        opacity: 1;
        visibility: visible;
      }
      &::before {
        opacity: 1;
        visibility: visible;
      }
    }
    .events-slider__img {
      &::after {
        border: 10px solid rgba(114, 221, 246, 0.8);
        filter: blur(20px);
      }
      &::before {
        opacity: 0;
        visibility: hidden;
      }
    }
    .events-slider__action {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
    }
  }
  .swiper-slide {
    .events-slider__item {
      transform: scale(0.7) !important;
    }
  }
  .swiper-slide-next,
  .swiper-slide-prev {
    .events-slider__item {
      transform: scale(0.8) !important;
    }
  }

  .swiper-slide-next-n1,
  .swiper-slide-prev-n1 {
    .events-slider__item {
      transform: scale(0.7) !important;
    }
  }
  .swiper-slide.swiper-slide-active {
    .events-slider__item {
      transform: scale(1) !important;
    }
  }

  @media (min-width: 320px) {
    padding: 37px 0 31px;
  }
  @media (min-width: 768px) {
    padding: 53px 0 43px;
  }
  @media (min-width: 1024px) {
    padding: 64px 0 60px;
  }
  @media (min-width: 1366px) {
    padding: 74px 0 50px;
  }
  @media (min-width: 1700px) {
    padding: 110px 0 70px;
  }
}

.events-hero {
  --sectionPaddingTop: 70px;
  padding-top: var(--sectionPaddingTop);
  padding-bottom: 0;
  margin-bottom: 100px;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("../img/events/events-hero.jpg");
  > * {
    position: relative;
    z-index: 2;
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        241.28deg,
        rgba(14, 14, 57, 0.75) 31.43%,
        rgba(22, 22, 66, 0.25) 70.06%,
        #1e1e4a 106.29%
      ),
      linear-gradient(0deg, rgba(21, 21, 73, 0.2), rgba(21, 21, 73, 0.2));
  }
  .events-hero__caption {
    max-width: 320px;
    margin: 0 auto;
    padding-top: 60px;
    padding-bottom: 88px;
    .section-title {
      max-width: 280px;
      text-align: center;
      margin: 0 auto 10px;
    }
    .section-text {
      font-size: clamp(18px, calc(1.125rem + ((1vw - 3.6px) * 0.7692)), 30px);
      margin-bottom: 40px;
    }
  }
  .events-single__labels {
    display: flex;
    justify-content: center;
    // column-gap: 8px;
    margin-bottom: 40px;
  }
  .events-card__date {
    padding: 0;
    padding-inline: 20px;
    gap: 8px;
    background: rgba(77, 122, 222, 0.35);
    box-shadow: 0px 0px 64px rgba(0, 154, 241, 0.3);
    backdrop-filter: blur(23px);
    border-radius: 50px;
    flex: 0 0 auto;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    opacity: 0.8;
    span {
      font-size: 14px;
      line-height: 140%;
    }
  }
  .events-hero__btn {
    text-align: center;
  }

  @media (min-width: 480px) {
    .events-hero__caption {
      max-width: 400px;
    }
  }
  @media (min-width: 768px) {
    --sectionPaddingTop: 88px;
    margin-bottom: 120px;
    .events-single__labels {
      margin-bottom: 35px;
      gap: 12px;
    }
    .events-card__date {
      height: 48px;
      span {
        font-size: 18px;
      }
    }
    .events-hero__caption {
      max-width: 450px;
      padding-bottom: 76px;
      padding-top: 22px;
      .section-title {
        max-width: 100%;
      }
    }
  }
  @media (min-width: 1024px) {
    margin-bottom: 130px;
    .events-single__labels {
      margin-bottom: 35px;
      gap: 10px;
    }
    .events-card__date {
      padding-inline: 32px;
      height: 56px;
    }
    .events-hero__caption {
      padding-top: 40px;
      padding-bottom: 106px;
      max-width: 540px;
      .section-text {
        margin-bottom: 72px;
      }
    }
  }
  @media (min-width: 1366px) {
    padding-top: 108px;
    margin-bottom: 160px;
    .events-hero__caption {
      padding-top: 60px;
      padding-bottom: 80px;
      max-width: 600px;
      .section-text {
        margin-bottom: 50px;
      }
    }
  }
  @media (min-width: 1700px) {
    padding-top: 155px;
    margin-bottom: 200px;
    .events-hero__caption {
      padding-top: 50px;
      padding-bottom: 220px;
      max-width: 700px;
      .section-title {
        margin-bottom: 20px;
      }
      .section-text {
        margin-bottom: 80px;
      }
    }
    .events-single__labels {
      margin-bottom: 40px;
    }
    .events-card__date {
      padding-inline: 40px;
      height: 76px;
      span {
        font-size: 24px;
      }
    }
  }
}

.webp {
  .events-hero {
    background-image: url("../img/events/events-hero.webp");
  }
}

.no-webp {
  .events-hero {
    background-image: url("../img/events/events-hero.jpg");
  }
}
